<template>
  <div id="app">

    <navigation></navigation>

    <!-- Top navigation -->

    <!-- Main website content -->
    <transition
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
      v-on:leave="leave"
      v-bind:css="false"
    >
      <router-view class="main"></router-view>
    </transition>

    <!-- Background shapes scene -->
    <scene></scene>
  </div>
</template>

<script>
  import { TimelineLite } from "gsap";
  // import router from './router.js'

  // Components
  import Nav from './Nav.vue';
  import Scene from './Scene.vue';

  export default {
    name: 'app',
    data(){
      return {
        tl: new TimelineLite({ paused: true })
      }
    },
    components: {
      "navigation": Nav,
      "scene": Scene
    },
    methods:{
      beforeEnter: function (el){

      },
      enter: function(el, done){
        console.log("transition entering");
      },
      leave: function(el, done){
        console.log("transition leaving");
        // this.tl.to({
        //
        // })
        // this.tl.onComplete = console.log("heyhey");
        done();

      }
    },
    created(){

      //  SVG Feature Detection
      const supportsCSSTransformsOnSVG = (() => {
        const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
        svg.setAttribute('viewBox', '0 0 2 2');
        Object.assign(svg.style, {
          position: 'absolute',
          top: 0,
          left: 0,
          width: '2px',
          height: '2px',
        });
        svg.innerHTML = '<rect width="1" height="1" style="transform: translate(1px, 1px)"/>';
        document.body.appendChild(svg);
        const result = document.elementFromPoint(1, 1) !== svg;
        svg.parentNode.removeChild(svg);
        console.log("SVG feature detection:");
        console.log(result);
        return result;
      })();

    },
    mounted(){
      this.tl.paused(true);
    },
    watch: {
      $route: function(to, from){
        const toDepth = to.path.split('/').length;
        const fromDepth = from.path.split('/').length;
        console.log(toDepth);
        console.log(fromDepth);
        if(toDepth < fromDepth) console.log('higher level');
        if(toDepth > fromDepth) console.log('lower level');
        if(toDepth == fromDepth) console.log('same level');
        // toDepth < fromDepth ? console.log('higher level') : console.log('lower level');
      }
    }
  }
</script>



